@primary-color: #1890ff; // 
@second-color: #BBDEFB; // 
@link-color: #1890ff; // 
@success-color: #52c41a; // 
@warning-color: #faad14; // 
@error-color: #f5222d; // 
@font-size-base: 14px; // 
@heading-color: rgba(0, 0, 0, 0.85); // 
@text-color: rgba(0, 0, 0, 0.65); // 
@text-color-secondary : rgba(0, 0, 0, .45); // 
@disabled-color : rgba(0, 0, 0, .25); // 
@border-radius-base: 4px; // /
@border-color-base: #d9d9d9; // 
@box-shadow-base: 0 2px 8px rgba(0, 0, 0, 0.15); // 
@box-shadow-bottom: 0px 2px 4px -2px #ccc; // 
@import '../../style/themes/index';
@import '../../style/mixins/index';

@message-prefix-cls: ~'@{ant-prefix}-message';

.@{message-prefix-cls} {
  .reset-component;

  position: fixed;
  top: 16px;
  left: 0;
  z-index: @zindex-message;
  width: 100%;
  pointer-events: none;

  &-notice {
    padding: 8px;
    text-align: center;
    &:first-child {
      margin-top: -8px;
    }
  }

  &-notice-content {
    display: inline-block;
    padding: @message-notice-content-padding;
    background: @component-background;
    border-radius: @border-radius-base;
    box-shadow: @shadow-2;
    pointer-events: all;
  }

  &-success .@{iconfont-css-prefix} {
    color: @success-color;
  }

  &-error .@{iconfont-css-prefix} {
    color: @error-color;
  }

  &-warning .@{iconfont-css-prefix} {
    color: @warning-color;
  }

  &-info .@{iconfont-css-prefix},
  &-loading .@{iconfont-css-prefix} {
    color: @info-color;
  }

  .@{iconfont-css-prefix} {
    position: relative;
    top: 1px;
    margin-right: 8px;
    font-size: @font-size-lg;
  }

  &-notice.move-up-leave.move-up-leave-active {
    overflow: hidden;
    animation-name: MessageMoveOut;
    animation-duration: 0.3s;
  }
}

@keyframes MessageMoveOut {
  0% {
    max-height: 150px;
    padding: 8px;
    opacity: 1;
  }
  100% {
    max-height: 0;
    padding: 0;
    opacity: 0;
  }
}

